@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
body {
 
  margin: 0;
  font-family: 'Syne Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 background-color: rgb(105 113 113 / 34%)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-heading {
  font-size: 40px;
}
.button__container {
    width: 100%;
    padding: 30px 0;
    background-color: rgb(51 51 51);
    margin-bottom: 30px;
    
}

.button {
    cursor: pointer;
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.726);
    padding: 20px;
    display: inline-flex;
    color: #bfbfbf;
    border-radius: 5px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.425);
    transition: all ease .3s;
}

.button:hover {
    background-color: black;
}

.button--false {
    background-color: rgba(197, 201, 201, 0.774);
    
}
.button--false:hover {
    background-color:rgba(145, 151, 151, 0.774);

}

  
.container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
   
}

.card-wrapper {
    border-radius: 10px;
    width: 350px;
    padding: 20px 0;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(23 34 49);
    box-shadow: 3px 3px 5px #000000e7;
    transition: all ease-in .09s;
    
}

.card-wrapper:hover {
    transform: scale(1.02);
    
}

.card-wrapper:active {
    transform: scale(.9);
}

.card__image {
    width: 300px;
    border-radius: 10px;
    box-shadow: 5px 5px 5px #0000005b;
    
}

.card__image-wrapper {
    -webkit-filter: grayscale(.4);
            filter: grayscale(.4);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all ease-in-out .2s;
}

.card__image-wrapper:hover  {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}



.card__description-trigger {
    position: relative;
}

.card__image-trigger {
    cursor: pointer;
    background-color: rgba(255, 255, 0, 0);
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0.2;
    z-index: 3;
}

.card__description-wrapper {
    border-radius: 10px;
    display: flex;
    align-items: center;
    position: absolute;
    color: white;
    left: 10px;
    right: 10px;
    background-color: rgba(0, 0, 0, 0.945);
}

.card_description-text {
   
    margin: 10px;
}

.grey {
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}
